.containerfiltertext {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 1vw;
  margin-bottom: 1vh;
  padding-bottom: 1vh;
  /* border: 1px solid yellow; */
  /* max-height: 200px; */

  @media (min-width: 400px) and (max-width: 660px) {
    overflow-y: visible;
    overflow-x: auto;
  }

  @media (max-width: 400px) {
    overflow-y: visible;
    overflow-x: auto;
  }
}

.containerfiltertext input {

  @media (min-width: 400px) and (max-width: 660px) {
    width: 20vw;
  }

  @media (max-width: 400px) {
    width: 30vw;
  }
}

.labelforfiltertext {
  text-transform: capitalize;
  font-weight: 600;

  @media (min-width: 962px) {
    font-size: 0.8vw;
  }

  @media (min-width: 778px) and (max-width: 962px) {
