.sidebar .activeUser .user #name .username {
  display: flex;
  flex-direction: row;
  font-size: 1.25vh;
  font-weight: bold;
  white-space: nowrap;
  overflow: hidden;
}

.sidebar .activeUser .user #name .role {
  margin-top: -0.75vh;
  font-size: 1vh;
  text-transform: capitalize;
  font-weight: lighter;
  text-align: left;
  color: var(--main-menu);
}

@media only screen and (max-width: 1080px) {
  .sidebar {
    overflow: hidden;
    background-image: var(--gradient-background2);
    width: 100%;
  }

  .sidebar .contRow {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 0.5vh;
    overflow-x: none;
    overflow-y: none;
    /* background-color: aliceblue; */
    /* border: 1px solid lawngreen; */
  }

  .sidebar .contRow .row {
    width: 25vw;
    display: flex;
    /* justify-content: center; */
    gap: 5px;
    align-items: center;
    margin: 10px
      /* border: 1px solid rebeccapurple; */
      /* background-color: yellow; */
  }

  .sidebar .contRow .row #icon {
    display: flex;
    justify-content: start;
    align-items: center;
    padding-right: 10px;
    /* border: 1px solid red; */
  }

  .sidebar .contRow .row #title {
    flex: 40%;
    text-align: center;
    font-size: 1.8vh;
    font-weight: bold;
    justify-content: start;
    overflow-wrap: break-word;
    padding-right: 10px;
    /* background-color: aqua; */
  }

  .sidebar .contRow .row #arrow {
    flex: 20%;
    place-items: center;
    color: var(--accent-color);
    transition: 0.7s;
    /* border: 1px solid red; */
  }

  .sidebar .contRowChild {
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 0 0.5vh;
    overflow-x: none;
    overflow-y: none;
    /* background-color: var(--hover-header); */
  }

  .sidebar .activeUser .user {
    padding-top: 1vh;
    padding-bottom: 1vh;
  }

  .sidebar .contRowChild .subRow {
    height: 8vh;
    /* background-color: aqua; */
    /* border: 1px solid red; */
    /* overflow: auto; */
  }

  .subRow #icon {
    font-size: 3vh;
  }

  .subRow #title {
    width: 15vh;
    margin-left: 0.5vh;
    margin-right: 1vh;
    padding: 0 1vw;
    display: flex;
    font-size: 1.8vh;
  }

  .sidebar .menu {
    margin-top: 0.5vh;
    /* background-color: lightgray; */
  }

  .sidebar .user {
    padding: 0 5vw 0 5vw;
    /* background-color: red; */
    flex-direction: column;
  }

  .sidebar .user #name {
    margin-left: 1vw;
    font-size: 1.8vh;
    letter-spacing: 0.5vw;
    text-transform: capitalize;
    /* background-color: aquamarine; */
  }


  .changePwd {
    font-size: 1.8vh;
    border: 1px solid white;
    border-radius: 5px;
    padding: 0vw 0vw 0vw 0.5vw;
    margin: 0vw 6vw 0vw 0vw;
    color: white;
  }

  .changePwd:hover {
    cursor: pointer;
    color: var(--header-color);
    background-color: white;
    border: 1px solid black;
  }
}

@media only screen and (min-width: 1081px) {
  .sidebar {
    display: grid;
    background-image: var(--gradient-background);
    grid-template-rows: 1fr 3fr 15fr 1fr;
    grid-template-areas:
      "button"
      "user"
      "menuRow"
      "version";
    overflow: none;
  }

  .sidebar .contRow {
    flex-direction: column;
    column-gap: 0;
    margin: 0;
    position: relative;
    overflow-y: auto;
    /* overflow-x: visible; */
  }

  .sidebar .contRow .row {
    width: 100%;
    height: 5.25vh;
    align-items: center;
    display: grid;
    white-space: nowrap;
    grid-template-columns: 31% 50% 19%;
    grid-template-areas: "logo title arrow";
  }

  .sidebar .contRow .row #icon {
    grid-area: logo;
    flex: 31%;
    display: grid;
    place-items: center;
  }

  .sidebar .contRow .row #title {
    grid-area: title;
    align-items: center;
    justify-content: left;
    font-size: 0.9em;
    overflow: hidden;
  }

  .sidebar .contRow .row #arrow {
    grid-area: arrow;
    display: grid;
    place-items: center;
  }

  .sidebar .contRow .subRow {
    height: 3vw;
    padding-left: 2em;
    flex-direction: row;
    white-space: nowrap;
  }

  .subRow #title {
    margin: 0 1vw;
    width: 200px;
    display: grid;
    font-size: 0.8em;
    overflow: hidden;
  }

  .subRow #icon {
    font-size: 1.25em;
  }

  .sidebar .menu {
    margin-top: 2vh;
  }

  .sidebar .activeUser .user {
    padding-top: 2vh;
    padding-bottom: 3vh;
  }

  .changePwd {
    font-size: 0.65vw;
    border: 1px solid white;
    border-radius: 5px;
    padding: 0vw 0vw 0vw 0.5vw;
    margin: 0vw 6vw 0vw 0vw;
    color: white;
  }

  .changePwd:hover {
    cursor: pointer;
    color: var(--header-color);
    background-color: white;
    border: 1px solid black;
  }
}

.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  /* background-color: var(--hover-header); */
  color: white;
  transition: 1s;
  z-index: 0;
  /* border:1px solid red; */
}

.sidebar::-webkit-scrollbar {
  display: none;
}

.sidebar .menu {
  grid-area: button;
  width: 100%;
  display: flex;
  color: white;
  /* border: 1px solid red; */
}

.sidebar .activeUser {
  grid-area: user;
  display: flex;
  flex-direction: column;
  /* border: 1px solid red; */
  padding: 0;
}

.sidebar .activeUser .welcome {
  margin-left: 2%;
  margin-bottom: -6%;
  font-size: 1.25vw;
  color: var(--accent-color);
  overflow: hidden;
  white-space: nowrap;
}

.sidebar .activeUser .user {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  text-transform: capitalize;
  overflow: hidden;
}

.sidebar .activeUser .user #pict {
  flex-basis: 3vw;
  display: grid;
  place-items: center;
  transition: 0.5s;
}

.sidebar .activeUser .user #name {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.sidebar .activeUser .user #login {
  flex: 3vw;
  display: grid;
  place-items: center;
}

.sidebar .contRow {
  padding-left: 5%;
  padding-right: 5%;
  width: 100%;
  /* background-color: magenta; */
}

.contRow::-webkit-scrollbar {
  display: none;
}

.sidebar .contRow .row {
  list-style: none;
  color: var(--main-menu);
  text-decoration: none;
  position: relative;
  transition: 1s;
}

.sidebar .version {
  grid-area: version;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  transition: 1s;
  white-space: nowrap;
}

.sidebar .version .info {
  display: flex;
  flex-direction: row;
}

.sidebar .version .info label {
  font-size: 0.8vw;
}

.sidebar .version .info div {
  font-size: 0.8vw;
  margin-left: 0.25vw;
}

.subRow {
  width: auto;
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--sub-memu);
  text-decoration: none;
  position: relative;
}

.subRow #icon {
  display: grid;
  place-items: center;
}

.row:hover {
  cursor: pointer;
  background-color: var(--hover-header);
  transition: 0.5s;
}

.subRow:hover {
  cursor: pointer;
  background-color: var(--hover-header);
  transition: 0.5s;
}

/* Tooltip text */
.row .tooltiptext,
.subRow .tooltiptext {
  visibility: hidden;
  display: none;
  width: auto;
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  text-align: center;
  padding: 0.5vw 0.75vw;
  border-radius: 0.5rem;
  font-size: 0.8rem;

  /* Position the tooltip text - see examples below! */
  position: fixed;
  left: 3vw;
  top: inherit;
  z-index: 100;
  /* top: 0; */
}


.changePwd .tooltiptext {
  visibility: hidden;
  display: none;
  width: auto;
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  text-align: center;
  padding: 0.5vw 0.75vw;
  border-radius: 0.5rem;
  font-size: 0.8rem;

  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 100;
  top: 10%;
  left: 58%;
}

.changePwd:hover .tooltiptext,
.row:hover .tooltiptext,
.subRow:hover .tooltiptext {
  visibility: visible;
  display: flex;
  flex-direction: row;
  /* opacity: 1; */
}

.icon {
  font-size: 2vw;
}

.dropdown-container {
  display: none;
  background-color: #262626;
  padding-left: 8px;
}